<template>
    <v-container>
        <v-form ref="form" @submit.prevent="start" id="form-start">
            <v-row justify="center">
                <v-col cols="12" md="6">
                    <v-row dense>
                        
                    </v-row>
                    <v-row dense>
                        <v-col cols="12">
                            <p class="title">Informasi Pribadi</p>
                        </v-col>
                        
                        <v-col v-for="(meta,idx) in person_information" :key="idx" cols="12">
                            <v-select v-if="meta.options" outlined dense :label="meta.label" :rules="rules" :items="meta.options" v-model="meta.value"/>
                            <v-text-field v-else-if="meta.ttl" dense outlined v-model="meta.value" :loading="loading" :rules="rules" :label="meta.label" hint="Medan, 18 November 2002" persistent-hint />
                            <v-text-field v-else dense outlined v-model="meta.value" :loading="loading" :rules="rules" :label="meta.label" />
                        </v-col>

                        <v-col cols="12" class="text-center">
                            <v-btn depressed class="primary" type="submit" form="form-start" :loading="loading" block>Submit</v-btn>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-form>
    </v-container>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    
    export default {
        name:'Start',
        data(){
            return{
                person_information:{
                    nama_lengkap:{
                        label:'Nama Lengkap',
                        value:'',
                    },
                    tempat_tanggal_lahir:{
                        label:'Tempat, Tanggal Lahir',
                        value:'',
                        ttl:true,
                    },
                    NIK:{
                        label:'NIK',
                        value:'' ,
                    },
                    jenis_kelamin:{
                        label:'Jenis Kelamin',
                        value:'',
                        options:['Laki-Laki','Perempuan']
                    },
                    pendidikan_terakhir:{
                        label:'Pendidikan Terakhir',
                        value:'',
                    },
                    lama_bekerja:{
                        label:'Lama Bekerja',
                        value:'',
                    },
                    pelajaran:{
                        label:'Bidang Studi',
                        value:'',
                    },
                },
                rules : [
                    v => !!v || 'This field is required'
                ],
            }
        },
        created(){
            // this.userDetail()
            this.person_information.nama_lengkap.value = this.detail.name
            this.person_information.pelajaran.value = this.detail.study
            this.person_information.lama_bekerja.value = this.detail.work_time
            if(localStorage.getItem("sequences")){
                this.$router.push({name:'sequence'})
            }
        },
        computed:{
            ...mapState({
                loading : state => state.loading,
                user: state => state.userLoggedIn,
                detail : state => state.detail,
                // otherData : state => state.auth.otherData,
            })
        },
        methods:{
            ...mapActions(['startExam']),
            // ...mapActions(['userDetail']),
            // ...mapMutations(['setDetail']),
            async start(){
                const meta = {
                    exam_id:this.detail.exam.id,
                    nama_lengkap:this.person_information.nama_lengkap.value,
                    tempat_tanggal_lahir:this.person_information.tempat_tanggal_lahir.value,
                    NIK:this.person_information.NIK.value,
                    jenis_kelamin:this.person_information.jenis_kelamin.value,
                    pendidikan_terakhir:this.person_information.pendidikan_terakhir.value,
                    lama_bekerja:this.person_information.lama_bekerja.value,
                    pelajaran:this.person_information.pelajaran.value,
                }
                if(this.$refs.form.validate() && this.detail){
                    var request = await this.startExam(meta)
                    localStorage.setItem('detail',JSON.stringify(request.data.detail))
                    this.$router.push({name:'sequence'})
                }
            },
            addItem(item){
                const newItem = new Object({
                    nama:{
                        label:'Nama',
                        value:'',
                    },
                    tahun:{
                        label:'Tahun',
                        value:'',
                    },
                    hasil:{
                        label:'Hasil',
                        value:'',
                    },
                })
                item.value.push(newItem)
            }
        },
    }
</script>

<style lang="scss" scoped>

</style>